.calculator {
  border: 1px rgb(76, 76, 76) solid;
  width: 30vw;
  overflow: hidden;
  display: grid;
  grid-template-rows: 0.3fr 1fr;
}
.calculator .container {
  display: flex;
  flex: 1;
  width: 100%;
  height: 100%;
}

@media screen and (max-width: 1024px) {
  .calculator {
    width: 80vw;
  }
}/*# sourceMappingURL=calculator.css.map */